<template> 
    <div class="personnalisation"> 
      <h1>Personnalisation</h1> 
      Le principal atout du site repose sur le personnalisation de chaque montre par les utilisateurs, en leur donnant une grande liberté de choix, ces derniers développeront un grand sentiment d’appartenance envers leur montre, étant donné que ce sont eux qui l’ont créé avec leurs propres goûts. 
      De plus, tous les éléments de la montre seront modifiables, ce qui signifie que chacun peut vraiment avoir une montre à son style, chose qui n’est pas anodin puisque une grande quantité de marque se focalise uniquement sur leurs propres modèles, ce qui peut potentiellement dissuader une partie des consommateurs potentiels, insatisfait de choix. 
      Ici, cette restriction est inexistante puisque la personnalisation est complète.

      <h2>Aperçu montre + choix couleurs (bracelet, boitier et écran)</h2>
      <h2>Bouton validation de la montre (consultable dans collection)</h2>

    </div> 
  </template> 
   
  <script> 
  export default { 
    name: 'personnalisation', 
    metaInfo: { 
      title: 'Personnalisation des montres', 
      meta: [{ 
            name: 'description', 
          }] 
    } 
  } 
  </script>


