<template> 
    <div class="collection"> 
      <h1>Collection</h1> 
      L'utilisateur connecté peut consulter les montres qu'ils a créer dans la page "Personnalisation", seules les 3 premières apparaissent.
      Si l'utilisateur souhaite voir toutes ses montres, il doit cliquer sur le bouton "Voir tout" pour accéder à la liste complète.

      <h2>Bouton "Voir tout"</h2>
    </div> 
  </template> 
   
  <script> 
  export default { 
    name: 'collection', 
    metaInfo: { 
      title: 'Collection des montres', 
      meta: [{ 
            name: 'description', 
          }] 
    } 
  } 
  </script>


